import { Deserializable } from '../../core/model/deserialize';

export enum UserType {
  PATIENTS  = 'patients',
  COUNSELLOR = 'counsellor',
  DOCTOR = 'doctor'
}

export interface IAuthPayload {
  username: string;
  password: string;
  userType: UserType;
}

export class User implements Deserializable{
  isSpecialUser: boolean;
  firstName: string;
  lastName: string;
  age: string;
  emailID: string;
  userID: string;
  planInfo: string;
  deviceID: string;
  videoID: string;

  get fullName() {
    return this.firstName+ ' '+ this.lastName;
  }

  deserialize(input: any) {
    Object.assign(this, input);
    return this;
  }
}
